import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			salesOrder:'',
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
			mer_id:0,
		};
	},
	
	onLoad(options){
		this.mer_id = options.mer_id;
		this.calcSize();
		getApp().user_api(true);
		this.getProductList();
	},
	onShow(){
	},
	//下拉刷新
	onPullDownRefresh() {
	},
	//加载更多
	onReachBottom(){
		// this.loadData();
	},
	methods: {
		
		getProductList(){
			let self = this;
			let path = '/gzh/combination_api/combination_list'
			let m_id = this.mer_id ? this.mer_id : '';
			let data = {
				mer_id:m_id,
				page:self.page,
				limit:self.limit
			};
			getApp().http(path, data, function(res) {
				console.log('详情',res);
				uni.hideLoading();
				
				let list = res;
				if (list.length > 0) {
					console.log('总条数',list.length)
					
					self.goodsList = self.goodsList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
					
					// self.status = list.length <= self.limit ? "more" : "";
					// if (self.status == "more") {
					// 	self.page += 1;
						
					// 	self.goodsList = self.goodsList.concat(list);
					// }
					
				} else {
					self.status = "";
				}
			})
		},
		indexUrl(icon){
			
			let http = icon.indexOf('http')
			// console.log('图谱爱',http)
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.getProductList();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".content");
					self.scrollHeight = res.windowHeight 
					// self.scrollHeight = 400
					// console.log(info);
					// console.log('啊啊啊啊',res);
				}
			});
		},
		
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			console.log('详情',item);
			let id = item.id;
			uni.navigateTo({
				url: `/pages/collage/collage?id=${id}`
			})
		},
		
		
		stopPrevent(){}
	},
}
